  margin: 0 auto;
  place-items: center;
  border: none;
  background: none;
  width: 100%;
  height: 6vh;
}

.caret span {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  font-size: 2vw;
  -webkit-transform: scaleX(200%);
          transform: scaleX(200%);
}

.team {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;

  -webkit-flex-direction: column;

          flex-direction: column;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: flex-end;
