/* Modal Buttons */
.btn {
  color: var(--yellow);
  font-size: 2vw;
  background: white;
  max-height: 5vw;
  max-width: 12vw;
  padding: 0.4vw 1vw;
  border: 0.2vw solid var(--blue-shadow);
  background-color: var(--blue);
  border-radius: 10px;
  margin: auto;
  box-shadow: 0 0 0 var(--blue-shadow);
  transition: all 0.2s;
  outline: none;
}

.reset {
  -webkit-filter: hue-rotate(120deg);
          filter: hue-rotate(120deg);
}

.btn:hover {
  box-shadow: 0.4vw 0.4vw 0 var(--blue-shadow);
  -webkit-transform: translate(-0.4vw, -0.4vw);
          transform: translate(-0.4vw, -0.4vw);
}

.btn:active {
  box-shadow: 0 0 0 var(--blue-shadow);
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.btn:disabled {
  opacity: 0.3;
}

.btn:disabled:hover {
  box-shadow: none;
  -webkit-transform: none;
          transform: none;
}

.btn.round-corners {
