.die-list {
  margin: 1vw;
  height: 6em;
  list-style-type: none;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 6em;
}
.even-roll {
  transition: -webkit-transform 1.5s ease-out;
  transition: transform 1.5s ease-out;
  transition: transform 1.5s ease-out, -webkit-transform 1.5s ease-out;
}
.odd-roll {
  transition: -webkit-transform 1.25s ease-out;
  transition: transform 1.25s ease-out;
  transition: transform 1.25s ease-out, -webkit-transform 1.25s ease-out;
}
.die-item {
  background-color: #fefefe;
  box-shadow: inset -0.35rem 0.35rem 0.75rem rgba(0, 18, 77, 0.3),
    inset 0.5rem -0.25rem 0.5rem rgba(0, 18, 77, 0.15);
  display: grid;
  grid-template-areas: "one two three" "four five six" "seven eight nine";
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  height: 100%;
  padding: 1em;
  position: absolute;
  width: 100%;
  opacity: 1;
}
.dot {
  -webkit-align-self: center;
          align-self: center;
  background-color: #676767;
  border-radius: 50%;
  box-shadow: inset -0.15rem 0.15rem 0.25rem rgba(0, 18, 77, 0.5);
  display: block;
  height: 1.25em;
  justify-self: center;
  width: 1.25em;
}
.even-roll[data-roll="1"] {
  -webkit-transform: rotateX(360deg) rotateY(720deg) rotateZ(360deg);
          transform: rotateX(360deg) rotateY(720deg) rotateZ(360deg);
}
.even-roll[data-roll="2"] {
